:root { color-scheme: dark; }
body { margin: 0; font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, Helvetica Neue, Arial, "Apple Color Emoji", "Segoe UI Emoji"; background: #0f172a; }
.page { display:flex; align-items:center; justify-content:center; height:100vh; }
.card { width: 380px; background:#0b1220; border:1px solid #1f2a44; border-radius:16px; box-shadow: 0 6px 24px rgba(0,0,0,.35); padding:28px; }
.title { font-size:24px; font-weight:700; color:#e5e7eb; text-align:center; margin-bottom:16px; }
.input { width:100%; margin:8px 0; padding:12px; border-radius:8px; border:1px solid #1f2a44; background:#0f172a; color:#e5e7eb; }
.button { width:100%; margin-top:12px; padding:12px; border-radius:8px; background:#6d28d9; color:white; border:none; cursor:pointer; }
.layout { display:grid; grid-template-columns: 320px 1fr; height:100vh; }
.sidebar { background:#0b1220; border-right:1px solid #1f2a44; display:flex; flex-direction:column; }
.sidebar-head { padding:12px; border-bottom:1px solid #14223b; }
.sidebar-title { color:#e5e7eb; font-weight:600; margin-bottom:8px; }
.search-input { width:100%; margin-bottom:12px; padding:10px; border-radius:8px; border:1px solid #1f2a44; background:#0f172a; color:#e5e7eb; }
.newchat { display:flex; gap:8px; }
.list { overflow:auto; }
.list-item { display:flex; align-items:center; gap:12px; padding:12px; border-bottom:1px solid #14223b; cursor:pointer; color:#e5e7eb; }
.list-item:hover { background:#0f1628; }
.list-item-text { display:flex; flex-direction:column; gap:4px; }
.list-item-title { font-weight:600; }
.list-item-sub { font-size:12px; opacity:.7; }
.avatar { width:36px; height:36px; border-radius:50%; display:flex; align-items:center; justify-content:center; color:#e5e7eb; font-weight:700; }
.main { display:grid; grid-template-rows:56px 1fr 64px; }
.header { background:#0b1220; border-bottom:1px solid #1f2a44; padding:12px; color:#e5e7eb; }
.header-user { display:flex; align-items:center; gap:12px; }
.header-title { font-weight:600; }
.header-sub { font-size:12px; opacity:.6; }
.messages { padding:20px; overflow:auto; }
.msg { max-width:70%; margin:8px 0; padding:10px 14px; border-radius:12px; background:#1f2937; color:#e5e7eb; }
.msg.mine { margin-left:auto; background:#065f46; color:#d1fae5; }
.composer { display:flex; gap:8px; padding:12px; border-top:1px solid #1f2a44; }
.composer input { flex:1; }
.composer .button { width:auto; padding:10px 16px; }
.empty-note { font-size:12px; color:#9ca3af; margin-top:6px; }
